/**
 * Theme Changer
 */
@media (max-width: $screen-sp) {
	body {
		padding-bottom: 60px;
	}
}
body.demo-mode {
	padding-bottom: 56px;
}
.theme-change {
	position: fixed;
	bottom: 70px;
	z-index: 1000000;
	width: 100%;
	padding: 5px 10px;
	background-color: #f0f0f0;
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	box-shadow: 0 -2px 5px -3px rgba(black, .2);

	@media (min-width: 768px) {
		bottom: 0;
		padding: 14px 15px;
	}

	&-row {
		@media (min-width: 768px) {
			display: flex;
			align-items: center;
			select {
				margin-right: 15px;
			}
			input[type="button"] {
				margin-right: 15px;
			}
		}
	}

	select {
		padding: 5px 8px;
		line-height: 1;
		font-size: 12px;
		background-color: #fff;
		border: 1px solid #ccc;

		@media (min-width: 768px) {
			font-size: 14px;
		}
	}
	input[type="button"] {
		padding: 5px 8px;
		line-height: 1;
		font-size: 12px;
		color: white;
		background-color: lighten(#4a9, 10%);
		border: 1px solid #4a9;

		&:hover {
			opacity: .8;
		}
	}
	.text {
		display: block;
		font-size: 12px;
		color: #888;

		@media (max-width: 767px) {
			margin-top: 8px;
		}
	}


}

