//  ---*/
.pointer {
	position: absolute;
	// top: 0;
	// left: 0;
	z-index: 99999;
	display: block;
	width: 48px;
	height: 48px;
	margin-top: -24px;
	margin-left: -24px;
	opacity: 0;
	background-color: rgba(#222, .3);
	border-radius: 48px;
	transform: scale(0);
	transition-property: transform;
	transition-duration: 140ms;
	transition-timing-function: ease-in-out;
	transition-delay: 80ms;
	&.act {
		opacity: 1;
		transform: scale(1);
	}
}

div {
	&.alignleft {
		float: left;
	}
	&.alignright {
		float: right;
	}
	&.aligncenter {
		margin-left: auto;
		margin-right: auto;
	}
}
img {
	&.alignleft {
		float: left;
		text-align: left;
	}
	&.alignright {
		float: right;
		text-align: right;
	}
	&.aligncenter {
		display: block;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
	}
}

.not-thumb {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	&:before {
		content: "\f03e";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: block;
		width: 30px;
		height: 28px;
		margin: auto;
		line-height: 1;
		font-family: "FontAwesome";
		font-size: 28px;
		color: #ccc;
	}
}

// List ---*/
.list-inline {
	margin-top: 0;
	margin-bottom: 0;
	margin-left: 0;
	padding-left: 0;
	list-style: none;
	& > li {
		display: inline-block;
		padding-left: 1px;
		padding-right: 1px;
	}
}
.list-table {
	margin: 0;
	padding: 0;
	@media (min-width: $screen-sp) {
		display: table;
		table-layout: auto;
		list-style: none;
		& > li {
			display: table-cell;
		}
	}
}


