.bread-list {
  position: relative;

	& > li {
		position: relative;
		// margin-right: -24px;
    // padding: 8px 20px 8px 40px;
    padding: 8px 12px;
		// border-left: 1px solid #ddd;
		// border-right: 1px solid #ddd;
