/* Archive Pager */
.pagination {
  width: 100%;
  margin: 64px 0;
  &__inner {
    display: flex;
    justify-content: center;

    > a,
    > span {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 8px;
      line-height: 1;
    }

    > a {
      transition: 235ms ease;
      &:hover {
        text-decoration: none;
      }
    }
  }

  .num {
    min-width: 30px;
    margin: 0 3px;
    font-size: 16px;
  }

  .prev {
    margin-right: 8px;
  }
  .next {
    margin-left: 8px;
  }

  .prev,
  .next {
    display: flex;
    align-items: center;
    width: 70px;
    .fa {
      font-size: 120%;
      line-height: 1;
    }
    .fa-angle-left {
      margin-right: 8px;
    }
    .fa-angle-right {
      margin-left: 8px;
    }
  }
}
