/* Single Pager */
.single-pager {
	ul {
		list-style: none;
		& > li {
			background-color: #ccc;
			a {
				display: block;
				padding: 8px 14px;
				color: #555;
				&:hover {
					opacity: .8;
				}
			}
			i {
				font-size: 120%;
			}
		}
	}
	.single-next {
		i {
			margin-right: 6px;
		}
	}
	.single-prev {
		i {
			margin-left: 6px;
		}
	}
	@include media(sp) {
		text-align: center;
		li {
			margin: 16px 0;
		}
	}
	@include media(tpc) {
		.single-next {
			float: left;
		}
		.single-prev {
			float: right;
		}
	}
}

// Page Pager ---*/
.page-pager {
	& > span, & > a {
		display: inline-block;
		padding: 3px 10px;
	}
	& > span {
		color: #fff;
		background-color: #222;
	}
	& > a {
		background-color: #ccc;
		&:hover {
			color: #fff;
			background-color: #222;
		}
	}
}

// Link Pages ---*/
.link-pages-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 64px -3px;
  padding: 2px 0;
  .link-pages-title {
    padding: 6px 12px;
  }
	> span {
		color: #fff;
		background-color: $primary-color;
	}
	> a {
		text-decoration: none;
		color: #454545;
		background-color: #f0f0f0;
		border: 1px solid #ddd;
		&:hover {
			color: #fff;
			background-color: $primary-color;
			border-color: $primary-color;
		}
	}
  > span,
  > a {
    display: flex;
    align-items: center;
    justify-content: center;
		// display: inline-block;
		min-width: 32px;
		margin: 3px;
		padding: 6px 8px;
		line-height: 1;
		font-size: 16px;
		text-align: center;
	}
}
