.channel-category {
	max-width: 800px;
	margin: 48px auto;
	// margin-top: 48px;
	// margin-bottom: 48px;
	padding: 24px 18px;
	text-align: center;
	background-color: #f5f5f5;
	.cat-link-title {
		margin: 0;
		margin-bottom: 16px;
		font-weight: normal;
		font-size: 18px;
		color: #888;
	}
	.cat-link {
		display: inline-block;
		margin: 0;
		list-style: none;
		text-align: left;
		& > li {
			display: inline-block;
			margin: 6px;
			a {
				display: inline-block;
				padding: 3px 6px;
				font-size: 12px;
				color: #888;
				background-color: #fcfcfc;
				border: 1px solid #dedede;
				border-radius: 4px;
			}
		}
	}
}

.channel-loop {
	display: flex;
	justify-content: center;
	flex-flow: row wrap;
	.item {
		position: relative;
		max-width: 360px;
		width: 100%;
		margin: 24px 15px;
	}
	@media (min-width: $screen-tab) and (max-width: $screen-pc - 1) {
		.item {
			width: 28.765%;
			&.single {
				width: 50%;
			}
		}
	}
	@media (min-width: $screen-pc) {
		.item {
			width: 30%;
			&.single {
				max-width: none;
				width: 68%;
			}
		}
	}
	.youtube {
		display: block;
	}
	.thumb {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 56.140351%;
		overflow: hidden;
		background-color: #282828;
		img {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 0;
			width: 100%;
			height: auto;
			margin: auto;
			opacity: 1;
			user-select: none;
			-webkit-touch-callout: none;
			transition: all 100ms ease-in;
		}
	}
	.yt_error {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: block;
		max-width: 100%;
		width: 100%;
		margin: auto;
		font-size: 13px;
		color: #fff;
		text-align: center;
		transform: translateY(48%);
	}
	.btn-play {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 2;
		display: block;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 64px;
		height: 64px;
		margin: auto;
		opacity: .6;
		user-select: none;
		-webkit-touch-callout: none;
		background-color: #fff;
		border-radius: 64px;
		box-shadow: 0 2px 5px 1px rgba(#222, .2),
								inset 0 -1px 2px 0 rgba(#888, .5),
								inset 0 1px 1px 0 rgba(#fff, .5);
		transition: all 100ms ease-in;
		&:before {
			content: "\f04b";
			display: inline-block;
			margin-left: 6px;
			font-family: "FontAwesome";
			font-size: 28px;
			color: #888;
		}
		
	}
	.item {
		transition: all 100ms ease-in;
		&:hover {
			.thumb {
				img {
					opacity: .8;
				}
			}
			.btn-play {
				opacity: .8;
				background-color: #b31217;
				&:before {
					color: #fff;
					text-shadow: 0 -1px 2px #888;
				}
			}
		}
	}
	.title {
		margin: 0;
		padding: 10px 2px;
		line-height: 1.4;
		font-weight: normal;
		font-size: 15px;
		border-bottom: 1px solid #888;
		.fa {
			margin-right: 8px;
		}
	}
	@media (min-width: $screen-tab) {
		.title {
			padding: 12px 2px;
			font-size: 16px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
		}
	}
}
