.social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  list-style: none;

  li {
    padding: 3px;
  }

  &.drawer-social {
    a {
      width: 34px;
      height: 34px;
      line-height: 32px;
      font-size: 136%;
      // &[href*="line.me"] {
      //   &:before {
      //     font-size: 150%;
      //   }
      // }
      &[href*="mixi.jp"] {
        &:before {
          font-size: 130%;
        }
      }
    }
  }
  a {
    & > span {
      display: none;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 34px;
    overflow: hidden;
    line-height: 1;
    font-size: 120%;
    color: #fff;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid transparent;
    border-radius: 100px;
    transition: 235ms ease;
    &:hover {
      color: #fff;
      opacity: .8;
      text-decoration: none;
    }

    // &:before {
    //   content: "\f0c1";
    //   font-family: "FontAwesome", "MiraiFont";
    //   line-height: 1;
    //   text-decoration: none;
    //   text-rendering: auto;
    //   -webkit-font-smoothing: antialiased;
    //   -moz-osx-font-smoothing: grayscale;
    // }
    &:before {
      content: "\f0c1";
    }

    &[href="#"] {
      border-color: #e7e7e7;
      &:before {
        content: "\f292";
      }
    }
    &[href*="facebook.com"] {
      background-color: $sns-color-facebook;
      &:before {
        // content: "\f09a";
        content: "\f39e";
      }
    }
    &[href*="instagram.com"] {
      position: relative;
      background-color: $sns-color-instagram;
      background-image: linear-gradient(155deg,#1400c8,#b900b4,#f50000);
      border: 0;
      &:before {
        // content: "\f16d";
        content: "\f16d";
        position: relative;
        z-index: 1;
      }
      &:after {
        content: "";
        position: absolute;
        left: -60%;
        bottom: -60%;
        display: block;
        width: 150%;
        height: 120%;
        background-image: radial-gradient(#ffdb2c 5%,#ff6922a6 55%,#ff586000 70%);
      }
    }
    &[href*="twitter.com"] {
      background-color: $sns-color-twitter;
      &:before {
        content: "\f099";
      }
    }
    &[href*="dropbox.com"] {
      &:before {
        content: "\f16b";
      }
    }
    &[href*="dribbble.com"] {
      &:before {
        content: "\f17d";
      }
    }
    &[href*="codepen.io"] {
      &:before {
        content: "\f1cb";
      }
    }
    &[href*="digg.com"] {
      &:before {
        content: "\f1a6";
      }
    }
    &[href*="flickr.com"] {
      &:before {
        content: "\f16e";
      }
    }
    &[href*="plus.google.com"] {
      background-color: $sns-color-google;
      &:before {
        content: "\f0d5";
      }
    }
    &[href*="github.com"] {
      &:before {
        content: "\f113";
      }
    }
    &[href*="pinterest.com"],
    &[href*="pinterest.jp"] {
      background-color: $sns-color-pinterest;
      &:before {
        content: "\f231";
      }
    }
    &[href*="getpocket.com"] {
      &:before {
        content: "\f265";
      }
    }
    &[href*="tumblr.com"] {
      &:before {
        content: "\f173";
      }
    }
    &[href*="vimeo.com"] {
      &:before {
        content: "\f27d";
      }
    }
    &[href*="wordpress.com"],
    &[href*="wordpress.org"] {
      &:before {
        content: "\f19a";
      }
    }
    &[href*="youtube.com"] {
      &:before {
        content: "\f167";
      }
    }
    &[href*="mailto:"] {
      &:before {
        content: "\f0e0";
      }
    }
    &[href*="youtube.com"] {
      background-color: $sns-color-youtube;
      &:before {
        content: "\f167";
      }
    }
    &[href$="/feed/"] {
      &:before {
        content: "\f09e";
      }
    }

    // MiraiFont
    &[href*="line.me"],
    &[href*="nav.cx"],
    &[href*="lin.ee"] {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      font-size: 236%;
      color: $sns-color-line;
      // background-color: $sns-color-line;
      background-color: #fff;
      border-radius: 8px;
      border: 0;
      &:before {
        // content: "\e909";
        content: "\f3c0";
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        // font-size: 150%;
        // line-height: 12px;
        line-height: 1;
        // background-color: #fff;
        // transform: translateY(19%);
      }
    }
    &[href*="mixi.jp"] {
      &:before {
        content: "\e90a";
        font-size: 130%;
        line-height: 30px;
      }
    }
  }

}
