.page-normal {
  padding: 3rem 0;

  @media (min-width: $lg-min) {
    padding: 6rem 0;
  }

  .c-title-page {
    margin-bottom: 4rem;

    @media (min-width: $lg-min) {
      margin-bottom: 6rem;
    }
  }
}

.blog-wrapper {
  margin-top: 28px;
  font-size: 1.4rem;
}
.post-archive-row {
  @media (min-width: $sm-min) {
    display: flex;
    flex-wrap: wrap;
  }
  @media (min-width: $md-min) {
    flex-direction: column;
  }
}
.post-archive {
  margin-bottom: 2em;
  overflow: hidden;
  background-color: #fff;
  border: 1px solid $default-color;
  border-radius: 2px;
  box-shadow: 0 2px 10px -2px rgba(#000, .1);

  @media (min-width: $sm-min) {
    width: 48%;
    margin: 0 1% 2em;
  }

  @media (min-width: $md-min) {
    width: 100%;
    margin: 0 0 2em;
  }

  .post-link {
    display: flex;
    flex-direction: column;
    transition: 235ms ease;

    @media (min-width: $md-min) {
      flex-direction: row;
    }

    &:hover {
      opacity: 0.8;
      text-decoration: none;
      background-color: #fdfdfd;

      .thumb {
        img {
          transform: scale(1.1);
        }
      }
    }
  }
  .thumb {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 52.25%;
    overflow: hidden;

    @media (min-width: $md-min) {
      width: 232px;
      height: 232px;
      padding-bottom: 0;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 235ms ease;
    }
  }
  .not-thumb {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #fcfcfc;
  }
  .post-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 12px;

    @media (min-width: $md-min) {
      padding: 16px;
    }
  }
  .post-header {
    display: flex;
    flex-direction: column-reverse;
  }
  .meta {
    display: flex;
    // justify-content: space-between;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 12px;
    padding-bottom: 12px;
    border-bottom: 1px solid #f8f8f8;
  }
  .cat {
    max-width: 100px;
    margin: 0 0 0 auto;
    padding: 2px 6px;
    font-size: 12px;
    color: #888;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    background-color: #fcfcfc;
    border: 1px solid #ddd;
    border-radius: 4px;

    @media (min-width: $md-min) {
      max-width: 300px;
    }
  }
  .title {
    $lh: 1.4;
    display: -webkit-box;
    /* autoprefixer: ignore next */
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    max-height: calc(2em * #{$lh});
    margin: 0 0 16px;
    line-height: $lh;
    font-weight: normal;
    font-size: 18px;

    @media (min-width: $md-min) {
      font-size: 20px;
    }
  }
  .datetime {
    line-height: 1;
    font-family: $font-google;
    font-size: 16px;
    color: #888;
    letter-spacing: 1px;

    @media (min-width: $md-min) {
      font-size: 20px;
    }
  }
  .content {
    $lh: 1.6;
    position: relative;
    display: -webkit-box;
    /* autoprefixer: ignore next */
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    max-height: calc(3em * #{$lh});
    margin: 0;
    line-height: $lh;
    font-size: 15px;
    color: #444;

    @media (min-width: $md-min) {
      -webkit-line-clamp: 2;
      max-height: calc(2em * #{$lh});
      font-size: 14px;
    }
  }

  .more {
    margin: 10px 0 0;
    padding: 12px 0 0;
    text-align: right;

    @media (min-width: $md-min) {
      margin-top: auto;
    }

    .btn {
      padding: 10px 16px;
      border: 0;
      border-radius: 0;
    }
  }
}

.contents-body {
  @extend %editor-area;
  @extend %clearfix;
}


.entry-row {
  padding-left: 15px;
  padding-right: 15px;

  &.has-blocks.full {
    padding-left: 0;
    padding-right: 0;
    .entry-header,
    .entry-footer,
    .comments-wrapper,
    .post-sidebar {
      max-width: 72rem;
      width: calc(100% - 4rem);
      margin-left: auto;
      margin-right: auto;
    }
  }

  .entry-content-widget-bottom {
    max-width: 500px;
    width: calc(100% - 4rem);
    margin-left: auto;
    margin-right: auto;

    .widget {
      margin-top: 4em;

      + .widget {
        margin-top: 2em;
      }
    }
  }

  @media (min-width: 992px) {
    max-width: 750px;
    margin: 0 auto;

    > div {
      padding: 0 15px;
    }
    &:not(.has-blocks) {
      .entry-header,
      .entry-footer {
        max-width: none !important;
      }
      .entry-content {
        > *:not(.entry-content-widget-bottom) {
          max-width: none;
        }
      }
    }
    .entry-content {
      > .wp-caption {
        max-width: 100% !important;
        height: auto;

        img {
          max-width: 100%;
          height: auto;
        }
      }
    }
    &.has-blocks.full {
      padding: 0;
      .post-wrapper {
        padding: 0;
      }
      .entry-header,
      .entry-footer,
      .comments-wrapper {
        max-width: 72rem;
        width: calc(100% - 4rem);
        margin-left: auto;
        margin-right: auto;
      }

      .post-sidebar {
        max-width: 72rem;
        width: calc(100% - 4rem);
        margin-top: 6rem;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
  @media (min-width: 1200px) {
    display: flex;
    max-width: 1170px;

    &.has-blocks.full {
      max-width: 100%;
      flex-direction: column;
    }

    &:not(.full) {
      .post-wrapper {
        width: 75%;
      }
      .post-sidebar {
        width: 25%;
      }
    }

  }
}

.post-single {
  .post-header {
    display: flex;
    flex-flow: column;
    // flex-flow: column-reverse;
    margin-bottom: 24px;
    padding-bottom: 12px;

  }
  .title {
    order: 2;
    margin-top: 0;
    padding-bottom: 8px;
    line-height: 1.4;
    font-size: 20px;
    border-bottom: 1px solid #ddd;

    @media (min-width: $md-min) {
      font-size: 24px;
    }
  }
  .meta {
    order: 1;
    display: flex;
    align-items: center;

    @media (max-width: $sm-max) {
      justify-content: space-between;
    }
  }
  .cat_tag {
    order: 3;
    p {
      &:first-child {
        margin-left: 0;
      }
    }
  }
  .datetime {
    width: 124px;
    font-family: $font-google;
    font-size: 16px;
    color: #666;

    @media (min-width: $md-min) {
      font-size: 20px;
    }
  }
  .cat, .tag {
    // display: inline-block;
    // float: left;
    display: inline;
    margin: 0;
    line-height: 2;

    @media (min-width: $md-min) {
      margin-left: 14px;
    }

    a {
      display: inline-block;
      line-height: 1;
      margin: 5px;
      padding: 4px 6px;
      font-size: 12px;
      background-color: #f5f5f5;
      border: 1px solid #ddd;
      border-radius: 4px;
    }
  }
  footer {
    margin-top: 40px;
  }

  .gallery {
    display: flex;
    flex-wrap: wrap;
    margin: 2em -2px;

    @for $i from 1 to 10 {
      &-columns-#{$i} {
        .gallery-item {
          flex-basis: (100% / $i);
        }
      }
    }

    .gallery-item {
      position: relative;
      padding: 2px;
    }

    .gallery-icon {
      position: relative;
    }

    .gallery-caption {
      position: absolute;
      left: 2px;
      right: 2px;
      bottom: 2px;
      padding: 5px;
      overflow: hidden;
      color: #fff;
      white-space: nowrap;
      text-shadow: none;
      text-overflow: ellipsis;
      background-color: rgba(#000, .5);
    }

    a {
      position: relative;
      display: block;
      height: 0;
      padding-bottom: 100%;
      overflow: hidden;
      transition: 235ms ease;

      &:hover {
        opacity: 0.8;

        img {
          transform: scale(1.1);
        }
      }
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 235ms ease;
      }
    }
  }

  img,
  .wp-caption {
    margin-bottom: 16px;
    &.alignleft {
      float: left;
    }
    &.alignright {
      float: right;
    }
    &.aligncenter {
      margin-left: auto;
      margin-right: auto;
    }
  }
}
.social-count {
  & > span {
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;
  }
  .fa-facebook, .fa-twitter {
    line-height: 1;
    font-size: 16px;
    color: #aaa;
  }
  .count {
    margin-left: 5px;
    font-size: 14px;
    color: #666;
  }
  @media (min-width: $screen-tab) {
    .fa-facebook, .fa-twitter {
      font-size: 18px;
    }
    .count {
      font-size: 16px;
    }
  }
}

.single-pager {
  margin-top: 42px;
  margin-bottom: 24px;
  padding-top: 36px;
  text-align: center;
  border-top: 1px solid #ddd;
  .next, .prev {
    display: inline-block;
    width: 50%;
  }
  @media (min-width: $screen-tab) {
    .next, .prev {
      width: 200px;
    }
  }
  .next {
    i {
      margin-right: 8px;
    }
  }
  .prev {
    i {
      margin-left: 8px;
    }
  }
  a {
    display: inline-block;
    line-height: 1;
    padding: 8px 16px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
}

.post-moreback {
  text-align: center;
  a {
    i {
      margin-right: 8px;
    }
  }
}

.search-found-wrapper {
  margin: 0 0 30px;
	padding: 14px;
	background-color: #f5f5f5;
	.left {
	}
	.head {
		font-weight: bold;
		font-size: 16px;
	}
	.keyword {
		margin-left: 3px;
		margin-right: 3px;
		font-weight: bold;
		font-size: 18px;
		color: #e85400;
	}
	.found {
		margin-left: 3px;
		margin-right: 3px;
		font-weight: bold;
		font-size: 16px;
		color: #e85400;
	}
	.search-found {
		display: block;
		width: 100%;
		text-align: right;
	}
	@media (min-width: $screen-tab) {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		.search-found {
			width: 200px;
			margin-left: auto;
		}
	}
}


.post-password {
}
.post-password-form {
  max-width: 480px;
  width: 100%;
  margin: 40px auto;

  p {
    font-size: 1.4rem;
  }

  .input-group {
    display: flex;
    margin-top: 12px;
    border: 1px solid #ccc;

    input[type=password] {
      flex: 1 1 0%;
      width: 100%;
      height: 40px;
      padding: 1px 8px;
      font-size: 1.6rem;
    }

    button[type=submit] {
      width: 92px;
      padding: 0;
      font-weight: bold;
      text-align: center;
      background-color: #d8d8d8;
      border-radius: 0;
    }
  }
}