html {
  font-size: 62.5%;
	// background-color: #232323;
}
body {
	@include family(normal);
  // font-size: 88%;
  font-size: 1.6rem;
	color: $text-color;
  // background-color: #222;
  scroll-behavior: smooth;
	touch-action: manipulation;
}
@media (max-width: $screen-sp) {
	body {
		max-width: 100%;
		overflow-x: hidden;
	}
}
@media (min-width: $screen-lg) {
	body {
		min-width: 1200px;
	}
}

// webkit scrollbar ---*/
// body::-webkit-scrollbar {
//   width: 0;
// }

::-webkit-input-placeholder {
	color: #999;
}
::-moz-placeholder {
	color: #999;
}
:-ms-input-placeholder {
	color: #999;
}

body,#drawer, #drawerContent, .drawer-content-back, .drawer-back {
	&::-webkit-scrollbar {
    width: 7px;
    height: 7px;
    background-color: $color-scrollbar;
    // box-shadow: inset 0 0 2px 2px rgba(#000, .3);
  }
  &::-webkit-scrollbar-track {
    border-radius: 0;
    // border: 1px solid #000;
    box-shadow: inset 0 0 2px 0 rgba(#000, .2);
  }
  &::-webkit-scrollbar-thumb {
    background-color: $color-scrollbar-thumb;
    border-radius: 0;
    box-shadow: 0 0 1px 0 rgba(#000, .2);
  }
}
@media (max-width: $screen-tab) {
	body::-webkit-scrollbar {
		width: 0;
	}
}

a {
	text-decoration: none;
	-webkit-tap-highlight-color: rgba(#a66, .2);
}


h1,h2,h3,h4,h5,h6 {
	// font-size: 100%;
}

// .tel-link
a[href^="tel:"] {
	pointer-events: none;

	@media (pointer: fine) {
		pointer-events: none;
		// color: currentColor;
		text-decoration: none;
	}

	@media (pointer: coarse) {
		pointer-events: auto;
	}
}