.tgl_child {
  > ul {
    display: flex;
    flex-wrap: wrap;

    li {
      // width: 33.333332%;
      flex-basis: 33.333332%;
    }
  }
}

.widget {
  margin-bottom: 2em;
  // 
  .thumb {
    display: block;
    overflow: hidden;
    transition: 300ms ease-in-out;
    &:hover {
      opacity: .8;
    }
    img {
      max-width: 100%;
      height: auto;
    }
    .not-thumb {
      position: relative;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #f4f4f4;
    }
  } // .thumb
  & > ul {
    margin-top: 1em;
  }
} // .widget
.widgettitle {
  margin: 0 0 16px;
  padding-bottom: 12px;
  font-size: 15px;
  // text-align: center;
  letter-spacing: 2px;
  border-bottom: 1px solid #ddd;

  @media (min-width: $md-min) {
    font-size: 18px;
  }
}

// Recent Posts
.widget_recent_entries,
.widget_popularposts {
  ul {
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      margin-bottom: 12px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__link {
    display: flex;

    &:hover {
      text-decoration: none;
      .title {
        text-decoration: none;
      }
    }
  }

  &__thumbnail {
    width: 72px;
    margin-right: 14px;

    .thumb {
      position: relative;
      padding-bottom: 100%;
      background-color: #f6f6f6;

      img,
      .not-thumb {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

      img {
        object-fit: cover;
      }

      .not-thumb {
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  &__content {
    flex: 1 1 0%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    width: auto;

    .datetime {
      order: 3;
      margin-bottom: 8px;
      line-height: 1;
      font-family: $font-google;
      font-size: 16px;
      color: #aaa;
    }

    .title {
      order: 2;
      display: -webkit-box;
      /* autoprefixer: ignore next */
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      max-height: calc(2em * 1.4);
      line-height: 1.4;
      font-size: 13px;
      text-decoration: underline;
      word-wrap: break-word;
      word-break: break-all;
    }
  }
  .pop_view {
    order: 1;
    margin-top: 8px;
    font-size: 10px;
    color: #a8a8a8;
  }
}

.widget_media_gallery {
  .gallery {
    display: flex;
    flex-wrap: wrap;
    margin: -1px;
  }
  .gallery-columns {
    @for $i from 1 to 10 {
      &-#{$i} {
        figure {
          // flex-basis: (100% / $i);
          width: (100% / $i);
        }
      }
    }
  }
  .gallery-item {
    padding: 1px;
    flex: 1 0 auto;

    a {
      position: relative;
      display: block;
      width: 100%;
      height: 0;
      padding-bottom: 100%;
      overflow: hidden;
      transition: 235ms ease;

      &:hover {
        opacity: 0.8;
        box-shadow: 0 0 1px 2px rgba(#666, .6);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      object-position: center center;
      object-fit: cover;
      transform-origin: 50% 0;

      @media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
        height: auto;
      }
    }
  }
}


.widget_media_image {
  // display: flex;
  // align-items: center;
  // justify-content: center;
  text-align: center;

  .widgettitle {
    text-align: left;
  }
  img {
    max-width: 100%;
    height: auto;
  }
  a {
    transition: 235ms ease;
    &:hover {
      opacity: 0.8;
    }
  }
}

.widget_newpost,
.widget_popular {
  ul {
    list-style: none;
  }
  li {
    display: table;
    width: 100%;
    margin: .5em 0;
    div {
      display: table-cell;
      text-align: left;
      vertical-align: top;

      &:first-child {
        width: 86px;
        @media (min-width: $md-min) {
          width: 90px;
        }
      }

    }
  }
  time {
    display: block;
    margin-bottom: 6px;
    line-height: .8;
    font-family: $font-google;
    font-size: 20px;
    color: #aaa;

    @media (min-width: $md-min) {
      font-size: 16px;
    }
  }
  .title {
    line-height: 1.4;
    font-size: 12px;
    color: $link-color;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
  &.topics {
    li {
      margin: 0 0 1.5em;
    }
    time {
      font-size: 20px;
      margin-bottom: 3px;
    }
  }

  .thumb {
    width: 62px;
    height: 62px;

    @media (min-width: $md-min) {
      width: 72px;
      height: 72px;
    }
  }
} // widget_newpost,widget_popular

// Search
.widget_search {
  label {
    display: none;
  }
  form {
    position: relative;
  }
  input[type="text"],
  input[type="submit"],
  button[type="submit"] {
    display: block;
    outline: 0;
    border: 0;
    border-radius: 0;
    box-sizing: border-box;
  }
  input[type="text"] {
    width: 100%;
    height: 32px;
    padding: 6px;
    line-height: 32px;
    font-size: 16px;
    vertical-align: middle;
    background-color: #ddd;
    &:focus {
      color: #555;
    }
  }
  button[type="submit"],
  input[type="submit"] {
    position: absolute;
    top: 0;
    right: 0;
    width: 32px;
    height: 32px;
    padding: 0;
    overflow: hidden;
    color: #fff;
    text-align: center;
    background-color: #555;
    transition: 300ms ease-in-out;
    i {
      font-size: 20px;
    }
    &:hover {
      opacity: .8;
    }
  }
}

// Categories
.widget_categories {
  ul {
    margin: 0;
    padding: 0;
  }
  ul {
    list-style: none;
  }
  li {
    margin: 0 0 4px;
    font-size: 14px;
    a {
      &:hover {
        opacity: .8;
      }
    }
  }
  & > ul {
    // margin-left: 6px;
  }
  .children {
    margin: 5px 0 8px 1em;
  }
}

// Archive
.widget_archive {
  ul,ol {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  li {
    font-size: 14px;
  }

  a {
    margin-left: 1em;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }

  .tgl {
    font-weight: bold;
    font-size: 16px;
    background: none;
    outline: none;
    border: none;
    span {
      display: inline-block;
      cursor: pointer;
    }
    i {
      margin-left: 6px;
      font-size: 20px;
      color: #aaa;
      transition: 200ms ease-in-out;
    }
    &.open {
      i {
        transform: rotateX(180deg);
      }
    }
  }
  .tgl_child {
    position: relative;
    display: none;
    margin: 5px 0 10px;
    // transition: max-height 440ms ease-in-out 0s;
    li {
      margin: 0 0 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
    &.open {
      // max-height: 800px;
    }
  }
}

// Tag cloud
.widget_tag_cloud {
}
.tagcloud {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  > a {
    margin: 5px;
