.btn {
	display: inline-block;
	margin-bottom: 0;
	padding: 6px 12px;
	line-height: 1.42857143;
	font-size: 14px;
	font-weight: normal;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	touch-action: manipulation;
	cursor: pointer;
	user-select: none;
	background-image: none;
	border: 1px solid transparent;
	// border-radius: 4px;
	transition: all 100ms ease-in;
	&:hover {
		text-decoration: none;
	}
}

.btn {
	// Default Color
	&-default {
		color: #222;
		background-color: #fff;
		border-color: #ccc;
	}
	// Primary Color
	&-primary {
		color: #222;
		background-color: #f6f6f6;
		border-color: #ddd;
	}
	// Secondary Color
	&-secondary {
		color: #888;
		background-color: #f5f5f5;
		border-color: #ddd;
	}
	// Shark Color
	&-shark {
		color: #eff5f8;
		background-color: #666;
		border-color: #666;
	}
	// Ace Color
	&-ace {
		color: #888;
		background-color: #fff;
		border-color: #ddd;
	}
	// Radius
	&-radius {
		border-radius: 100%;
	}
}

.btn {
	// XL
	&-xl {
		font-size: 112%;
		padding: 16px 22px;
	}
	&-ll {
		padding: 12px 20px;
	}
	&-lm {
		padding: 10px 18px;
	}
	&-ls {
		padding: 8px 16px;
	}
	&-mid {
		font-size: 100%;
		padding: 6px 12px;
	}
	&-sl {
		font-size: 96%;
		padding: 6px 8px;
	}
	&-sm {
		font-size: 88%;
		padding: 5px 8px;
	}
	&-ss {
		font-size: 80%;
		padding: 4px 6px;
	}
}

.btn {
	.fa {
		margin-left: 4px;
		margin-right: 4px;
		// font-size: 108%;
	}
}

// Catalog Carousel ---*/
.btn-catalog-info {
	position: absolute;
	top: 8px;
	left: 8px;
	z-index: 2;
	width: 42px;
	height: 42px;
	color: #fff;
	background-color: rgba(#f38, 1);
	outline: none;
	border-radius: 100%;
	box-shadow: 0 1px 2px 0 rgba(#222, .3);
	transition: all 160ms ease-in;
	&:focus {
		outline: none;
	}
	&:hover {
		background-color: rgba(#f38, .8);
	}
}
