$drawer-width: 220px;
// Drawer Navigation ---*/
@media (max-width: $screen-tab) {
  #drawerOverlay {
    position: fixed;
    top: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    height: 100vh;
    opacity: 0;
    background-color: rgba(#222, .7);
    transition: 480ms ease-in-out 0s;
    &.drawer-open {
      // right: $drawer-width;
      z-index: 110;
      // width: 100%;
      opacity: 1;
      transform: translateX(-$drawer-width);
    }
  }
}

@media (max-width: $screen-tab) {
  #drawerContent {
    position: relative;
    height: 100%;
  }
  .drawer-content-back {
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
}

#drawer {
  // position: absolute;
  position: relative;
  top: 0;
  z-index: 10;
  // height: 100%;
  // overflow: hidden;
  padding: 0;
  background-color: #fff;
  background-position: center;
  background-repeat: repeat;
  transition: transform 320ms ease-in 20ms;
  transform: translateX(0);
  transform-origin: left center;
  @media (max-width: $screen-tab) {
    max-width: 100%;
    overflow-x: hidden;
    // background-image: url("./common/images/common/bg_main-sp.jpg");
  }
  &.drawer-open {
    @media (max-width: $screen-tab) {
      transform: translateX(-$drawer-width);
      #drawerContent {
        overflow: hidden;
        -webkit-overflow-scrolling: none;
      }
    }
    & ~ .drawer-navi {
      opacity: 1;
    }
  }
}


// 
.drawer {
  &-btn {
    cursor: pointer;
    user-select: none;
    -webkit-touch-callout: none;
    position: fixed;
    top: 10px;
    right: 100%;
    // left: -64px;
    z-index: 50;
    display: block;
    width: 54px;
    height: 48px;
    margin-right: 15px;
    padding:  0;
    white-space: nowrap;
    line-height: 1;
    font-size: 10px;
    color: #585858;
    text-align: center;
    vertical-align: middle;
    background-color: rgba(#fff, .7);
    outline: 0;
    opacity: 1;
    transition: 240ms ease-in-out 100ms;
    i {
      margin-right: 4px;
    }
    .bars {
      position: absolute;
      top: 7px;
      left: 0;
      right: 0;
      width: 50%;
      height: 18px;
      margin: 0 auto;

      .bar {
        display: block;
        width: 100%;
        height: 3px;
        margin: 3px 0;
        background-color: currentColor;
        border-radius: 2px;
        transition: 235ms ease-out;
      }
    }
    .text {
      position: absolute;
      left: 0;
      bottom: 3px;
      width: 100%;
      line-height: 1;
      font-family: monospace;
      font-weight: 500;
      font-size: 10px;
      color: currentColor;
      text-align: center;
    }
    &.drawer-open {
      .bars {
        .bar {
          &:nth-child(1),
          &:nth-child(3) {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
          }
          &:nth-child(1) {
            transform: rotate(-45deg);
          }
          &:nth-child(3) {
            transform: rotate(45deg);
          }
          &:nth-child(2) {
            position: absolute;
            top: 50%;
            width: 0;
            margin-top: -2px;
          }
        }
      }
    }
  }
  &-btn-close {
    // float: right;
    // display: block;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 100%;
    margin-left: auto;
    padding: 0;
    // line-height: 44px;
    user-select: none;
    -webkit-touch-callout: none;
    background-color: #222;
    outline: 0;
    border: 0;
    border-left: 1px solid #000;
    border-radius: 0;
    &:hover {
      background-color: #922;
    }
  }
}
// 
.drawer {
  &-navi {
    @extend .clearfix;
    position: fixed;
    top: 0;
    right: 0;
    // left: 100%;
    z-index: 11;
    // opacity: 0;
    width: $drawer-width;
    height: 100%;
    height: 100vh;
    background-color: #eaeaea;
    // box-shadow: inset 4px 0 3px -1px rgba(#222, .3);
    transition: transform 300ms ease-in-out 10ms;
    transform: translate3d(100%, 0, 0);
    &:before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      z-index: 2;
      width: 1px;
      height: 100vh;
      background-color: #fff;
      box-shadow: 2px 0 5px 0px rgba(#222, .6);
    }

    &.drawer-open {
      transform: translate3d(0, 0, 20px);
    }
    .logo-area {
      position: relative;
      display: flex;
      align-items: center;
      height: 46px;
      padding: 0 0 0 12px;
      user-select: none;
      -webkit-touch-callout: none;
      letter-spacing: 2px;
      // line-height: 40px;
      font-family: $font-google;
      font-size: 20px;
      color: #fff;
      @include sgradient(
        $colors: (
          (lighten($navi-color-mode, 10%), 0%),
          ($navi-color-mode, 100%)
        )
      );
      border-bottom: 1px solid #000;
      img {
        max-width: 62%;
      }
      @media (min-width: $screen-sp + 1) and (max-width: $screen-tab) {
        height: 48px;
        line-height: 48px;
      }
    }
  }
  &-inner {
    position: relative;
    height: calc(100% - 48px);
    background-color: #eaeaea;
    .drawer-back {
      height: 100%;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
    }
    nav {
      position: relative;
      // padding-top: 10px;
      padding-bottom: 80px;
      overflow: hidden;
      box-sizing: border-box;
    }
    ul {
      list-style: none;
      & > li {
        margin: 0;
        padding: 0;
      }
    }

    .social-icon {
      margin-top: 20px;
      margin-bottom: 20px;
      padding: 10px 5px;
      text-align: center;
      & > li {
        // margin-top: 8px;
        // margin-bottom: 8px;
        padding: 4px;

        a {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 34px;
          height: 34px;
          line-height: 1;
          // font-size: 136%;
        }

        .ico {
          width: 34px;
          height: 34px;
          border-radius: 100%;
        }
        .fa {
          line-height: 34px;
          // font-size: 24px;
        }
        .fa-line {
          &:before {
            // vertical-align: 6px;
          }
        }
      }
    }
  }
}
.drawer-gnavi {
  margin: 0;
  > li {
    position: relative;
    &.current-menu-item {
      background-color: #e0e0e0;
    }
    > a {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column-reverse;
      padding: 10px 8px;
      user-select: none;
      -webkit-touch-callout: none;
      font-family: serif;
      font-size: 14px;
      color: $navi-color-jp;
      text-align: center;
      border-bottom: 1px solid #ccc;

      > .ja {
        font-size: 14px;
        color: $navi-color-jp;
      }
      > .en {
        font-family: $font-google;
        font-size: 20px;
        color: $navi-color-en;
      }
      &:hover {
        text-decoration: none;
      }
      &.hover {
        color: #e6e6e6;
        background-color: #222;
        > .ja {
          color: #e6e6e6;
        }
        > .en {
          color: #e6e6e6;
        }
      }
    }
  }
} // ul.drawer-gnavi

.drawer-subnavi {
  > li {
    border-bottom: 1px solid #ccc;
    > a {
      display: block;
      padding: 14px 8px;
      font-size: 18px;
      color: #fff;
      text-align: center;
      background-color: #444;
      -webkit-touch-callout: none;
      i {
        margin-right: 8px;
      }
      &:hover {
        text-decoration: none;
        background-color: #222;
      }
    }
  }
}




