.gnavi-block {
  position: relative;
  margin-top: 14px;
  // margin-bottom: 24px;
  @media (max-width: 768px) {
    display: none;
  }
  .container-fullid {
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
    .row {
      margin-left: 0;
      margin-right: 0;
    }
  }
}
.gnavi {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  table-layout: fixed;
  text-align: center;
  & > li {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-grow: 1;
    a {
      color: $navi-color-mode;
      text-decoration: none;
    }
    &:last-child {
      & > a {
        &:after {
          border-right: 1px solid $navi-color-line;
        }
      }
    }
    & > a {
      // display: block;
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      justify-content: center;
      padding: 12px 0 10px;

      // font-family: sans-serif;
      font-size: 12px;
      color: $navi-color-jp;
      &:hover {
        background-color: #f8f8f8;
      }
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: block;
        // width: 100%;
        height: 51.4%;
        margin: auto;
        box-sizing: border-box;
        border-left: 1px solid $navi-color-line;
        // border-right: 1px solid $navi-color-line;
      }
      &:before {
        // content: attr(title);
        // display: block;
        // margin-top: 2px;
        // letter-spacing: 1px;
        // font-family: $font-google;
        // font-size: 20px;
        // color: $navi-color-en;
      }

      > .ja {
        font-size: 12px;
        color: $navi-color-jp;

        &.not-en {
          line-height: 1.2;
          font-size: 18px;
          color: $navi-color-en;
        }
      }
      > .en {
        // margin-top: 2px;
        line-height: 1.2;
        font-family: $font-google;
        font-size: 20px;
        color: $navi-color-en;
        letter-spacing: 1px;
      }
    }
  }
}
