// Concept ---*/
.concept {
	padding: 62px 0;
	.logo {
		max-width: 480px;
		margin: 0 auto 3rem;
		text-align: center;
		img {
			max-width: 100%;
			height: auto;
			vertical-align: top;
		}
	}
	.text {
		text-align: center;
		p {
			line-height: 2;
			margin-bottom: 1em;
    }

    img {
      max-width: 100%;
      height: auto;
    }
	}
	@media (max-width: $screen-tab - 1) {
		padding-top: 0;
		.logo {
			display: none;
		}
		.text {
			text-align: left;
		}
	}
}




// .front-info ---*/
.front-info {
	padding-top: 28px;
	padding-bottom: 28px;
	overflow: hidden;
	background-color: rgba(#eee, .5);
	.c-title-sm {
		margin: 0;
	}
	.item {
		margin-top: 20px;
		margin-bottom: 20px;
	}
	.datetime {
		display: block;
		line-height: 1.4;
    font-size: 12px;
    @media (min-width: $screen-tab) {
      font-size: 14px;
    }
	}
	.title {
		margin: 0;
		line-height: 1.4;
		font-weight: normal;
    font-size: 12px;
    @media (min-width: $screen-tab) {
      font-size: 14px;
    }
    @media (min-width: $screen-pc) {
      padding-left: 24px;
    }
	}
  @media (max-width: $screen-tab - 1) {
    &-more {
      font-size: 16px;
      text-align: right;
    }
  }
  @media (min-width: $screen-tab) {
    .container {
      & > .row {
        width: 100%;
        display: table;
        & > div {
          float: none;
          display: table-cell;
          vertical-align: middle;
        }
        & > .front-info-more {
          text-align: center;
          vertical-align: bottom;
        }
      }
    }
  }
}

// Freespace ---*/
.bannerspace {
	padding: 48px 0;

	&__row {
		display: flex;
    flex-direction: column;
    margin: -15px;
		@media (min-width: $screen-sm) {
      flex-direction: row;
      justify-content: center;
      flex-wrap: wrap;
		}

	}
  &__item {
    padding: 15px;

    @media (min-width: $screen-sm) {
      width: 33.333332%;
    }
  }

  &__figure {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    overflow: hidden;

    img {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      max-width: none;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    & > a {
      transition: all 100ms ease-in;
      &:hover {
        opacity: .8;
      }
    }
    figcaption {
      position: absolute;
      bottom: 0;
      z-index: 1;
      width: 100%;
      padding: 8px 16px;
      font-family: $font-google-jp, "", YuGothic, "", Meiryo;
      font-size: 18px;
      color: #fff;
      background-color: rgba(#222, .6);
    }
  }
}

// Blog ---*/
.front-blog {
  padding: 36px 0;

  &__row {
    @media (min-width: $md-min) {
      display: flex;
      flex-wrap: wrap;
      margin: -16px;
    }
    @media (min-width: $lg-min) {
      justify-content: center;
    }
  }

  &__item {
    @media (min-width: $md-min) {
      width: 50%;
      padding: 16px;
    }
    @media (min-width: $lg-min) {
      width: 25%;
    }
  }

  &__article {
    position: relative;

    @media (max-width: $sm-max) {
      max-width: 480px;
      margin: 0 auto 16px;
      box-shadow: 1px 2px 4px 0 rgba(#111, .1);
    }

    .postcard {
      display: block;
    }
  }

  &__article-link {
    position: relative;
    display: block;
    transition: 235ms ease;

    @media (max-width: $sm-max) {
      display: flex;
      align-items: flex-start;
    }

    &:hover {
      opacity: 0.8;
      text-decoration: none;
    }

    .thumb {
      position: relative;
      display: block;
      height: 100px;
      overflow: hidden;
      background-color: #fcfcfc;

      @media (max-width: $sm-max) {
        width: 120px;
      }

      @media (min-width: $md-min) {
        height: 0;
        padding-bottom: 50.625%;
      }
      @media (min-width: $lg-min) {
        padding-bottom: 60%;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .not-thumb {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;

      &:before {
        content: "\f03e";
        line-height: 1;
        font-family: "FontAwesome";
        font-size: 28px;
        color: #ccc;
      }
		}

    .title {
      $lh: 1.6;
      display: -webkit-box;
      /* autoprefixer: ignore next */
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      height: calc(2em * #{$lh});
      margin: 0;
      line-height: $lh;
      font-weight: bold;
      font-size: 15px;

      @media (min-width: $md-min) {
        margin: 8px 0 8px;
        font-size: 14px;
      }
    }

    .datetime {
      margin: 0 0 8px;
      line-height: 1;
      font-size: 13px;
      color: #666;
    }
    .new {
      position: absolute;
      top: 2px;
      left: -6px;
      z-index: 1;
      display: inline-block;
      height: 14px;
      padding: 0 8px 0 12px;
      line-height: 16px;
      font-size: 10px;
      border-radius: 2px 2px 2px 0;
      filter: drop-shadow(2px 2px 2px rgba(#222, .5));

      @media (min-width: $md-min) {
        top: 8px;
        left: -5px;
        height: 20px;
        line-height: 22px;
        font-size: 12px;
      }

      &:before {
        content: "";
        position: absolute;
        top: 100%;
        left: 0;
        display: block;
        width: 0;
        height: 0;
        border: 3px solid darken(#f05240, 15%);
        border-bottom-color: transparent;
        border-left-color: transparent;
      }
    }

    .body {
      @media (max-width: $sm-max) {
        display: flex;
        flex-direction: column-reverse;
        flex: 1;
        padding: 12px 12px;
      }
      @media (min-width: $md-min) {
        padding: 0 12px 8px;
      }
    }
  }

  &__more {
    margin: 40px 0 0;
    text-align: center;
  }
}

// Catalog ---*/
.front-catalog-wrap {
	overflow: hidden;
	.thumb {
		position: relative;
		overflow: hidden;
		a {
			display: block;
			user-select: none;
			-webkit-touch-callout: none;
			-webkit-tap-highlight-color: transparent;
			&.hover {
				.overlay {
					opacity: 1;
				}
			}
		}
		@media (max-width: $screen-tab) {

		}
		@media (min-width: $screen-tab + 1) {
			.btn-catalog-info {
				display: none;
			}
			a {
				&:hover {
					.overlay {
						opacity: 1;
					}
				}
			}
		}
	}
	.not-thumb {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 152.112676%;
	}
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		background-color: rgba(#fff, .6);
		transition: all 320ms ease-in-out;
	}
	.text {
		position: absolute;
		bottom: 10%;
		width: 100%;
		padding: 10px;
		text-align: center;
		box-sizing: border-box;
	}
	.title {
		line-height: 1.6;
		font-weight: bold;
		font-size: 14px;
		color: #222;
	}
	.stylist {
		font-size: 12px;
		color: #222;
	}
	.new {
		position: absolute;
		top: 5px;
		right: -17px;
		z-index: 1;
		padding: 2px 26px 0 10px;
		font-size: 12px;
		border-radius: 2px 0 0 2px;
		transform: rotate(-38deg);
		box-shadow: -1px 2px 2px 0 rgba(#222, .5);
	}
	@media (max-width: $screen-tab - 1) {
		.new {
			top: 1px;
			right: -15px;
			padding: 2px 20px 0 6px;
			font-size: 10px;
		}
	}
	@media (min-width: $screen-tab + 1) {
		.title {
			font-size: 16px;
		}
	}
	.catalog-container {
		position: relative;
		max-width: 1440px;
		width: 100%;
		height: 100%;
		margin: 18px auto;
		padding-bottom: 12px;
		// overflow: hidden;
	}
	.catalog-swiper {
		overflow: hidden;
	}
	.swiper-slide {
		position: relative;
		width: 180px;
		font-size: 18px;
		text-align: center;
		background: #f0f0f0;
		img {
			width: 100%;
			height: auto;
		}
	}
	.swiper-button-prev,
	.swiper-button-next {
		display: none;
	}
	@media (min-width: $screen-sp + 1) {
		.swiper-slide {
			width: 200px;
		}
	}
	$border-width: 2px;
	@media (min-width: $screen-tab + 1) {
		.swiper-slide {
			width: 284px;
		}
		.swiper-button-prev,
		.swiper-button-next {
			top: 0;
			display: block;
			width: 14px;
			height: 100%;
			margin: 0;
			user-select: none;
			-webkit-touch-callout: none;
			background-image: none;
			transition: opacity 100ms ease-in;
			&:before {
				content: "";
				position: absolute;
				top: 50%;
				display: block;
				width: 36px;
				height: 36px;
				border-top: $border-width solid $swiper-border-color;
			}
			&:hover {
				opacity: .8;
			}
		}
		.swiper-button-prev {
			left: 0;
			&:before {
				left: 0;
				border-left: $border-width solid $swiper-border-color;
				transform: rotate(-45deg) translateX(32px);
			}
		}
		.swiper-button-next {
			right: 0;
			&:before {
				right: 0;
				border-right: $border-width solid $swiper-border-color;
				transform: rotate(45deg) translateX(-32px);
			}
		}
	}
	@media (min-width: 1440px) {
		.swiper-button-prev {
			transform: translateX(-54px);
		}
		.swiper-button-next {
			transform: translateX(54px);
		}
	}
}
.front-catalog {
  &__more {
    padding: 0 15px;
    text-align: center;
  }
}


// Front Social ---*/
.front-social {
  padding: 36px 0 60px;

  @media (min-width: $lg-min) {
    padding: 48px 0 60px;
  }

  &__row {
    @media (max-width: $sm-max) {
      max-width: 500px;
      margin: 0 auto;
    }
    @media (min-width: $md-min) {
      display: flex;
      justify-content: center;
      margin: 0 -15px;
    }
  }

  &__item {
    @media (max-width: $sm-max) {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    @media (min-width: $md-min) {
      max-width: 530px;
      width: 50%;
      flex: 1;
      padding: 0 15px;
    }

    .title {
      margin: 0 0 20px;
      padding-bottom: 12px;
      font-size: 28px;
      text-align: left;
      border-bottom: 1px solid #ddd;
    }

    .content {
      overflow: hidden;
      text-align: center;
      a {
        display: block;
        transition: 235ms ease;

        &:hover {
          opacity: 0.8;
        }
      }
      img {
        max-width: 100%;
        height: auto;
      }
    }
  }
}
