.topics-archive {
	margin-top: 28px;
	padding-bottom: 28px;
	border-bottom: 1px solid #ddd;
	&:first-child {
		margin-top: 0;
	}
	@media (max-width: $screen-tab) {
		max-width: 320px;
		margin-left: auto;
		margin-right: auto;
	}
}
.topics-content {
	.title {
		margin: 0 0 14px;
		font-size: 18px;
	}
	.datetime {
		display: block;
		margin-bottom: 10px;
		font-size: 16px;
	}
	.content {
		margin-bottom: 14px;
		line-height: 1.6;
	}
	.btn {
		float: right;
	}
	@media (max-width: $screen-tab - 1) {
		.content {
			position: relative;
			min-height: 32px;
			max-height: 64px;
			overflow: hidden;
			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				z-index: 1;
				display: block;
				width: 100%;
				height: 36px;
				@include sgradient(
					$colors:(
						(rgba(#fff, 0), 0%),
						(rgba(#fff, 1), 100%)
					)
				);
			}
		}
	}
	@media (min-width: $screen-tab) {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		.datetime {
			font-family: $font-google;
			font-size: 20px;
		}
		.topics-header {
			width: calc(100% - 120px);
			margin-left: 20px;
		}
		.title {
			font-size: 19px;
		}
		.content {

		}
	}
}



