// Hair Catalog ---*/
#catalog {

}

.stylist-category {
  max-width: 800px;
  margin: 24px auto;
  overflow: hidden;
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-radius: 4px;
  .title {
    // display: inline-block;
    // float: left;
    // width: 120px;
    margin: 0;
    padding: 6px 14px;
    line-height: 32px;
    font-size: 15px;
    color: $text-color-header;
    text-align: center;
    border-bottom: 1px solid #ddd;
    .slash {
      display: inline-block;
      margin-left: 18px;
      margin-right: 6px;
    }
  }
  .list-inline {
    // display: inline-block;
    // margin-left: 120px;
    padding: 10px;
    background-color: #fcfcfc;
    .current {

    }
    & > li {
      margin: 6px 4px;
      a {
        display: inline-block;
        padding: 6px 12px;
        font-size: 12px;
        color: $btn-secondary-color;
