// Catalog Search ---*/
.stylist-category {
  .title {
    cursor: pointer;
    user-select: none;
    -webkit-touch-callout: none;
    &:hover {
      .toggle {
        color: #fc8989;
      }
    }
  }
}
.catalog-search-wrapper {
  background-color: #fff;
  transition: max-height 1000ms ease-in-out 0s;

  .cs_submit {
    padding: 10px 14px 24px;
    text-align: center;
    .btn-lm {
      padding-left: 48px;
      padding-right: 48px;
      font-size: 18px;
    }
  }
  @media (max-width: $screen-tab - 1) {
    max-height: 0;
    // height: 0;
    overflow: hidden;
    &.open {
      max-height: 5000px;
      // height: auto;
    }
  }
  @media (min-width: $screen-tab) {
    // max-height: 0;
    // // height: 0;
    // overflow: hidden;
    // &.open {
    //   max-height: 5000px;
    //   // height: auto;
    // }
  }
}
.catalog_search {

  padding: 14px;
  &:first-child {
    border-top: none;
  }
  .head {
    @media (min-width: $md-min) {
      padding-top: 2px;
    }
  }
  .radio {
    display: flex;
    flex-wrap: wrap;
    width: auto;
  }
  .cr {
    label {
      cursor: pointer;
      display: flex;
      align-items: center;
      // margin: 5px;
      margin: 0 5px 5px 0;
      padding: 5px 10px;
      line-height: 1;
      font-size: 13px;
      background-color: #fff;
      user-select: none;
      -webkit-touch-callout: none;
      &:before {
        content: "";
        display: flex;
        align-items: center;
        justify-content: center;
        width: 1em;
        height: 1em;
        margin-right: 8px;
        line-height: 0;
        font-family: "FontAwesome";
        color: #454545;
        text-align: center;
        vertical-align: middle;
        background-color: #fff;
        border: 1px solid #ddd;
        border-radius: 100px;
        // box-shadow: inset 1px 1px 3px 0 rgba(#666, .2);
      }
    }
    input {
      display: none;
      &:checked {
        & + label {
          color: $btn-default-color;
          background-color: $btn-default-bg-color;
          &:before {
            content: "\f00c";
            font-size: 12px;
          }
        }
      }
    }

  }
  &.searchTag {
    .cr {
      label {
        &:before {
          border-radius: 4px;
        }
      }
    }
  }

  @media (max-width: $screen-tab - 1) {
    .head {
      font-size: 16px;
      border-bottom: 1px dotted #ddd;
    }
  }
  @media (min-width: $screen-tab) {
    .head {
      float: left;
      width: 120px;
    }
    .radio {
      margin-left: 120px;
    }
  }
}


// Catalog LoopContent ---*/
.catalog-loop {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: stretch;
  max-width: 480px;
  margin: 0 auto;
  padding: 0 5px;

  @media (min-width: $md-min) {
    max-width: none;
  }

  &__item {
    width: 50%;
    padding: 5px;

    @media (min-width: $md-min) {
      width: 33.333332%;
      padding: 10px;
    }
    @media (min-width: $lg-min) {
      width: 25%;
    }
  }

  &__item-inner {
    display: flex;
    height: 100%;
    background-color: #fff;
    // border-radius: 3px;
    box-shadow: 0 1px 3px 0px rgba(#333, .2);
    transition: all 240ms ease-in-out 0;
  }

  &__item-link {
    flex: 1;
    position: relative;
    display: block;
    width: 100%;
    transition: 235ms ease;

    &:hover {
      opacity: 0.8;
      text-decoration: none;

      .thumb {
        img {
          transform: scale(1.1);
        }
      }
      .meta-box {
        opacity: 1;
        visibility: visible;
      }
    }

    .thumb {
      position: relative;
      height: 0;
      padding-bottom: 122.8125%;
      overflow: hidden;

      img,
      .not-thumb {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
      }
      img {
        object-fit: cover;
        object-position: center top;
        transform-origin: 50% 0;
        transition: 235ms ease;
      }
      .not-thumb {
        background-color: #f6f6f6;
      }
    }

    .border-catalog {
      margin: 0;
      height: 1px;
      background-color: #ddd;
      border: none;
    }

    .meta-box {
      display: flex;
      flex-direction: column;
    }

    .style-name {
      display: -webkit-box;
      /* autoprefixer: ignore next */
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      max-height: calc(2em * 1.4);
      margin: 10px;
      line-height: 1.4;
      font-weight: normal;
      font-size: 14px;
      .fa {
        margin-right: 6px;
      }
    }

    .meta {
      margin: 0;
      padding: 10px;
      list-style: none;
    }

    .manage {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 -5px 10px;
      font-size: 10px;
      span {
        margin: 5px;
        padding: 3px 4px;
        line-height: 1.2;
        white-space: nowrap;
        text-overflow: ellipsis;
        border: 1px solid #ccc;
        border-radius: 4px;
      }
    }

    .staff {
      line-height: 1.6;
      font-size: 14px;
      text-align: center;

      @media (min-width: $md-min) {
        font-size: 16px;
      }
    }
  }
}


// Single ---*/
.catalog-single {
  @media (min-width: $md-min) {
    display: flex;
  }
  &__image {
    margin-bottom: 30px;
    @media (min-width: $md-min) {
      width: 305px;
      margin-right: 30px;
      margin-bottom: 0;
    }
  }

  &__data {
    @media (min-width: $md-min) {
      flex: 1;
    }
  }
}

.catalog-picture {
  @media (max-width: $sm-max) {
    display: flex;
    flex-direction: row-reverse;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  &-body {
    position: relative;
    @media (max-width: $sm-max) {
      width: 67.8%;
    }
    img {
      width: 100%;
      height: auto;
    }
    &.not-length {
      margin-left: auto;
      margin-right: auto;
    }
  }
  &-thumbnails {
    @media (max-width: $sm-max) {
      width: 27.2%;
      margin-right: 5%;
    }
  }
  .pic-wrap {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 127.78%;
    overflow: hidden;
    background-color: #fafafa;
    transition: all 100ms ease-in;
  }
  .pic {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    transition: all 600ms ease-in-out;
    &.active {
      opacity: 1;
      z-index: 2;
    }
  }
  &-list {
    position: relative;
    display: flex;
    flex-direction: column;

    @media (min-width: $md-min) {
      flex-direction: row;
      justify-content: space-between;
      margin-top: 10px;
    }

    &-item {
      position: relative;
      display: block;
      width: 100%;
      overflow: hidden;
      user-select: none;
      -webkit-touch-callout: none;
      border-radius: 3px;
      border: 1px solid transparent;
      transition: border 235ms ease-out;

      @media (max-width: $sm-max) {
        + .catalog-picture-list-item {
          margin-top: 8px;
        }
      }

      @media (min-width: $md-min) {
        width: 32%;
      }

      img {
        pointer-events: none;
        max-width: 100%;
        height: auto;
      }

      &.active {
        border-color: $default-color;
      }
    }
  }

  .not-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
    letter-spacing: 1px;
    font-family: monospace;
    font-size: 2vw;
    color: rgba(#555, .3);
    background: #e0e0e0;
    span {
      display: block;
      text-align: center;
      line-height: 1;
    }
  }
}

// Catalog Content ---*/
.catalog-header {
  .title {
    margin-bottom: 12px;
    padding-bottom: 12px;
    line-height: 1.4;
    font-size: 20px;
    border-bottom: 1px solid #ddd;

    @media (min-width: $screen-tab) {
      margin-top: 0;
      font-size: 26px;
    }
  }
  .comment-wrapper {
    // &:after {
    //   content: "";
    //   display: block;
    //   clear: both;
    // }

    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;

    @media (min-width: $screen-tab) {
      margin-top: 22px;
    }
  }
  .comment {
    flex: 1 1 0%;
    position: relative;
    min-height: 80px;
    padding: 8px;
    font-size: 12px;
    background: #f5f5f5;
    border-radius: 10px;

    @media (min-width: $screen-sp + 1) {
      padding: 14px 18px;
    }

    @media (min-width: $screen-tab) {
      margin-bottom: 22px;
      padding: 20px 26px;
      font-size: 14px;
      line-height: 2;
    }

    &:not(.comment-full) {
      // margin-right: 100px;
      @media (min-width: $screen-tab) {
        // margin-right: 120px;
      }
      &:after {
        content: "";
        position: absolute;
        top: 32px;
        left: 100%;
        display: block;
        width: 0;
        height: 0;
        border: 12px solid transparent;
        border-left: 14px solid #f5f5f5;
      }
    }
  }

  .staff-pic {
    float: right;
    width: 100px;
    text-align: center;

    figcaption {
      @media (min-width: $screen-tab) {
        margin-top: 8px;
      }
    }

    img {
      width: 100%;
      height: auto;
      // border-radius: 16px;
    }
    .thumb {
      display: block;
      width: 80px;
      height: 80px;
      margin-left: auto;
      margin-right: auto;
      overflow: hidden;
      background-color: rgba(#ccc, .3);
      border-radius: 16px;
    }
    .name {
      font-size: 12px;
      i {
        margin-left: 4px;
      }
    }
  }
}

