}

.catalog-footer {
  position: relative;
  margin-top: 32px;
  .other-title {
    padding-top: 8px;
    padding-left: 15px;
    padding-bottom: 10px;
    font-family: $font-google;
    font-weight: normal;
    font-size: 27px;
    color: #666;
    border-bottom: 1px solid #ddd;
  }
  .read-more {
    position: absolute;
    top: 6px;
    right: 15px;
  }
}
.other-catalog {
  margin-top: 32px;
  padding-bottom: 12px;
  overflow: hidden;
  .swiper-wrapper {
    list-style: none;
  }
  .swiper-slide {
    width: 140px;
    a {
      display: block;
      width: 100%;
      height: 0;
      padding-bottom: 127.717391%;
      overflow: hidden;
      background-color: rgba(#ccc, .3);
      transition: all 100ms ease-in;
      &:hover {
        opacity: .8;
      }
    }
    .not-thumb {
      display: block;
      width: 100%;
      height: 0;
      padding-bottom: 127.717391%;
      background-color: rgba(#ccc, .3);
    }
    img {
      width: 100%;
      height: auto;
    }
  }
  @media (min-width: $screen-tab) {
    .swiper-slide {
      width: 184px;
    }
  }
}


