// Staff Archives ---*/
.staff-loop {
  margin: 48px 0;
}
.staff-flex {
  display: flex;
  flex-wrap: wrap;

  .item {
    width: 100%;
    padding: 10px;

    @media (min-width: $sm-min) {
      width: 33.333332%;
    }
    @media (min-width: $lg-min) {
      width: 25%;
    }

    article {
      position: relative;
      display: flex;
      flex-direction: column;
      height: 100%;
      overflow: hidden;
      background-color: #fff;
      box-shadow: 0 2px 5px 0 rgba(#000, .1);
    }
  }

  &__link {
    display: block;
    flex: 1 0 auto;

    &:hover {
      text-decoration: none;

      .thumb {
        img {
          transform: scale(1.1);
        }
      }
    }
  }

  .thumb {
    position: relative;
    display: block;
    height: 0;
    padding-bottom: 122.5%;
    overflow: hidden;

    .thumb-box {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center top;
      transform-origin: 50% 0;
      transition: 350ms ease;

      @media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
        height: auto;
      }
    }
  }
  .not-thumb {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #f6f6f6;
  }


  .meta-box {
    position: relative;
    z-index: 1;
    display: block;
    width: 100%;
    padding: 10px;
    // border-bottom: 1px solid #ddd;
  }
  .staff-name {
    width: 100%;
    margin: 0;
    overflow: hidden;
    font-weight: normal;
    font-size: 15px;
    white-space: nowrap;
    text-overflow: ellipsis;

    @media (min-width: $md-min) {
      font-size: 18px;
      text-align: left;
    }
    .fa {
      margin-right: 6px;
    }
  }
  .manage {
    width: 100%;
    margin: 8px 0 0;
    overflow: hidden;
    font-size: 12px;
    color: #888;
    white-space: nowrap;
    text-overflow: ellipsis;

    @media (min-width: $md-min) {
    }
  }
  .reserve {
    text-align: center;
    .btn {
      display: block;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 12px;
      border: none;
      border-radius: 0;
    }
  }
}


// Staff Single ---*/
.staff-single {
  &__row {
    @media (min-width: $md-min) {
      display: flex;
    }
  }

  &__picture {
    @media (min-width: $md-min) {
      width: 320px;
      margin-right: 40px;
    }
  }

  &__data {
    @media (min-width: $md-min) {
      flex: 1;
    }
  }
}
.staff-picture {
  @media (max-width: $sm-max) {
    max-width: 280px;
    margin: 0 auto;
  }
  .pic {
    position: relative;
    height: 0;
    padding-bottom: 127.5%;
    overflow: hidden;
    .not-thumb {
      position: absolute;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center top;
    }
  }
}
.staff-social {
  padding-top: 8px;
  padding-bottom: 8px;
}
.staff-header {
  position: relative;
  text-align: center;

  @media (min-width: $md-min) {
    text-align: left;
  }
  .title {
    margin: 0;
    padding: 10px 0;

    @media (max-width: $sm-max) {
    }

    @media (min-width: $md-min) {
      padding: 0 0 10px;
      border-bottom: 1px solid #ddd;
    }

    > span {
      @media (max-width: $sm-max) {
        display: inline-block;
        padding: 4px;
      }
    }
  }
  .name {
    font-size: 18px;

    @media (min-width: $md-min) {
      font-size: 24px;
    }

    @media (min-width: $lg-min) {
      font-size: 30px;
    }
  }
  .kana {
    font-weight: normal;
    font-size: 12px;

    @media (min-width: $md-min) {
      font-size: 14px;
    }

    @media (min-width: $lg-min) {
      margin-left: 24px;
      font-size: 18px;
    }
  }
  .manage {
    margin: 8px 0 26px;
    font-size: 12px;
    > span {
      display: inline-block;
      padding: 4px;
      border: 1px solid #ddd;
      border-radius: 4px;

      @media (min-width: $md-min) {
        padding: 6px 10px;
      }
    }
  }

  .reserve {
    text-align: center;

    @media (min-width: $md-min) {
      position: absolute;
      top: 0;
      right: 0;
      display: inline-block;
    }
  }
}
.staff-table {
  width: 100%;
  margin-top: 24px;
  margin-bottom: 24px;
  th,td {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 13px;
    border-bottom: 1px solid #ddd;
  }
  th {
    width: 100px;
    vertical-align: top;
    font-weight: normal;
    color: #888;

    @media (min-width: $md-min) {
      width: 140px;
    }
  }
  td {
    padding-left: 8px;
  }
  .blog {
    th,td {
      padding-top: 14px;
      padding-bottom: 14px;
    }
    td {
      a {
        word-break: break-all;
      }
    }
  }
}
.staff-comment {
  margin: 0 -15px;
  padding: 15px;
  background-color: #f5f5f5;

  @media (min-width: $md-min) {
    margin: 0;
    padding: 24px 28px;
    line-height: 1.8;
  }
  p {
    font-size: 13px;
  }
}


// Other Staff ---*/
.other-staff {
  padding: 24px 0 48px;
  background-color: #eee;

  @media (min-width: $md-min) {
    padding: 56px 0 48px;
  }

  .title {
    font-weight: normal;
    color: #666;
    text-align: center;
    span {
      display: block;
    }
    .en {
      line-height: 1.32;
      letter-spacing: 1px;
      font-family: $font-google;
      font-size: 24px;
    }
    .jp {
      line-height: 2;
      font-size: 11px;
    }

    .sp-br {
      display: none;
      margin-left: 8px;

      @media (min-width: $md-min) {
        display: block;
      }
    }
  }
  .center {
    text-align: center;
  }
}
.other-staff-list {
  margin: 0 -15px;
  padding: 0 0 24px;

  @media (min-width: $md-min) {
    margin: 0;
  }
  .staff-list {
    list-style: none;
    & > li {
      width: auto;
    }
  }
  .item {
    display: block;
    max-width: 126px;
    color: #888;
    text-align: center;
    transition: all 100ms ease-in;
    &:hover {
      opacity: .8;
    }
  }
  .thumb {
    width: 126px;
    height: 126px;
    overflow: hidden;
    border-radius: 126px;
    border: 3px solid #fff;
    img {
      width: 100%;
      height: auto;
    }
  }
  .name {
    display: block;
    line-height: 2;
    margin: 8px 0;
    font-size: 13px;
    .fa {
      margin-right: 6px;
    }
  }
  .manage {
    display: inline-block;
    padding: 4px 6px;
    font-size: 11px;
    background-color: #fcfcfc;
    border: 1px solid #ddd;
    border-radius: 3px;
  }
  .swiper-pagination {
    bottom: 0;
  }
  .swiper-pagination-bullet-active {
    background-color: #666;
  }
  .staff-list-next,
  .staff-list-prev {
    top: auto;
    bottom: 0;
    width: 28px;
    height: 28px;
    overflow: hidden;
    font-size: 18px;
    text-align: center;
    background: $default-color;
    border-radius: 100%;
    transform: none;

    @media (max-width: $sm-max) {
      display: none;
    }
    &:before {
      line-height: 28px;
      font-family: "FontAwesome";
    }
  }
  .staff-list-next {
    right: 0;
    &:before {
      content: "\f105";
    }
  }
  .staff-list-prev {
    left: 0;
    &:before {
      content: "\f104";
    }
  }
}

.staff-other-catalog {
  .swiper-wrapper {
    .swiper-slide {
      width: 184px;
      @media (min-width: $md-min) {
        width: 232px;
      }
      a {
        padding-bottom: 127.586207%;
      }
    }
  }
}

