.comments-area {
  margin-top: 60px;
  padding: 16px;
  background-color: #fff;
  border: 1px solid #e0e0e0;

  @media (min-width: $md-min) {
    padding: 20px;
  }

  .comments-title {
    display: flex;
    justify-content: space-between;
    margin: 0 0 2rem;
    padding-bottom: 8px;
    font-size: 1.8rem;
    border-bottom: 1px solid #e0e0e0;

    .k {
      font-size: 80%;
    }
  }

  .no-comments {
    margin: 3rem 0;
    padding: 1rem;
    font-size: 1.5rem;
    text-align: center;
    border: 1px dotted #e8e8e8;
  }

  .navigation {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 3rem 0;

    .section-heading {
      width: 100%;
      margin: 0 0 1.4rem;
    }
    .nav-previous,
    .nav-next {
      // width: 50%;
      padding: 0 10px;
    }
    .nav-next {
    }
  }
}
.comment-list {
  margin: 0;
  // margin-left: 2.4rem;
  padding: 0;
  // padding-left: 4rem;
  list-style: none;

  .comment {
    // margin: 0 0 3rem;
    border-left: 1px dotted #e8e8e8;
    border-top: 1px dotted #e8e8e8;

    &::marker {
      font-size: 90%;
    }
  }

  .children {
    margin-top: 2rem;
    // padding-left: 4rem;
    padding-left: 2rem;
    list-style: none;
  }

  [class*="depth-"] {
    // margin-left: 3rem;
    // padding-left: 4rem;
  }

  .even {
    background-color: #fffff8;
  }
  .odd {
    background-color: #fff;
  }

  .comment-meta {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    margin: 0 0 1.4rem;
    border-bottom: 1px dotted #ddd;
  }
  .comment-author {
    .avatar {
      width: 48px;
      height: 48px;
      margin: 0;
      margin-right: 10px;
      object-fit: cover;
      border-radius: 48px;
    }
    .says {
      font-size: 80%;
    }
    .url {
      text-decoration: underline;
    }
  }
  .comment-metadata {
    margin-left: 1rem;
    font-size: 80%;
  }

  .comment-awaiting-moderation {
    width: 100%;
    margin: 5px 0;
    padding: 8px;
    font-weight: 700;
    font-size: 1.3rem;
    background-color: #fff0f0;
    border: 2px solid #ffe0e0;
    border-radius: 4px;
  }

  .comment-body {
    padding: 1.2rem 1.6rem;

    .reply {
      margin-top: 8px;
      text-align: right;
    }
    .comment-reply-link {
      display: inline-block;
      padding: 6px 12px;
      line-height: 1.2;
      font-size: 1.2rem;
      color: #38e;
      background-color: #e8e8e8;
      border: 1px solid #ddd;

      &:hover {
        text-decoration: none;
        background-color: #dadada;
        border-color: #ccc;
      }
    }
  }

  .pingback {

  }
}

.comment-content {
  font-size: 1.5rem;

  h1, h2, h3, h4, h5, h6 {
    margin: 4rem 0 1.6rem;
  }

  ul, ol {
    padding-left: 24px;
  }
  pre {
    margin: 2rem 0;
    padding: 1rem;
    font-size: 1.4rem;
    background-color: #f0f0e8;
  }
  p {
    margin: 1em 0;
  }

  * {
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

// 
.comment-respond {
  margin-top: 5rem;

  &:first-child {
    margin-top: 0;
  }

  .comment-reply-title {
    margin: 0 0 2rem;
    padding-bottom: 8px;
    border-bottom: 1px dotted #e0e0e0;
  }
  #cancel-comment-reply-link {
    float: right;
    padding: 5px 12px;
    line-height: 1.2;
    font-size: 1.3rem;
    border: 1px dotted #888;

    &:hover {
      opacity: 0.8;
    }
  }

  .must-log-in {
    a {
      color: #335ae0;
    }
  }
}

.comment-form {
  padding: 1.4rem 1.6rem;
  background-color: #f8f8f8;
  p {
    margin: 1em 0;
  }

  .comment-notes {
    font-size: 1.3rem;
  }
  .required {
    color: #f00;
  }
  [class*="comment-form-"] {
    label {
      font-weight: bold;
      font-size: 1.4rem;
    }
    textarea,
    input[type=text],
    input[type=email],
    input[type=url] {
      display: block;
      max-width: 100%;
      min-height: 40px;
      display: block;
      padding: 3px 6px;
      font-size: 1.6rem;
      // background-color: #fff;
      background: transparent no-repeat left bottom;
      background-image: linear-gradient(180deg, #fff 0%, #fff 85%, #eee 100%);
      background-size: 100% 0;
      border-bottom: 2px solid #000;
      box-sizing: border-box;
      transition: 500ms ease;

      &:focus {
        background-size: 100% 100%;
      }
    }

    input[type=email] {
      width: 25em;
    }

    textarea,
    input[type=url] {
      width: 100%;
    }
  }

  .comment-form-cookies-consent {
    display: flex;
    align-items: center;
    margin: 2em 0;

    input[type=checkbox] {
      margin-right: 8px;
      width: 18px;
    }
    label {
      cursor: pointer;
    }
  }

  .form-submit {
    margin: 2rem 0 0;
    input[type=submit] {
      cursor: pointer;
      padding: 12px 16px;
      line-height: 1.2;
      font-weight: 700;
      border: 2px solid #000;

      &:hover {
        opacity: 0.8;
      }
    }
  }
}