.sp-fixed-contact {
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 10;
	width: 100%;
	padding-bottom: calc(constant(safe-area-inset-bottom) + 10px);
	padding-bottom: calc(env(safe-area-inset-bottom) + 10px);
	overflow: hidden;
	background-color: #f8f8f8;
	border-top: 1px solid #ddd;
	box-shadow: 0 0 2px -1px rgba(black, .1);
	transform: translateY(0);
	transition: transform 235ms ease-in-out 240ms;

	@media (min-width: 769px) {
		display: none;
	}

	&.scrolldown {
		transform: translateY(100%);
		transition: transform 235ms ease-in-out 40ms;
	}

	&-inner {
		display: flex;
		justify-content: center;
		align-items: center;

		.item {
			position: relative;
			flex: 1;
			padding: 5px;
			text-align: center;

			+ .item {
				&:before {
					content: "";
					position: absolute;
					top: 50%;
					left: 0;
					display: block;
					width: 1px;
					height: 70%;
					background-color: #ccc;
					transform: translateY(-50%);
				}
			}

			a {
				display: block;
				padding: 5px 10px;

				&:active,
				&:hover {
					text-decoration: none;
				}

				&:active {
					color: #f00;
				}
				&:hover {
					.fa {
						color: #f00;
					}
				}

				.fa {
					display: block;
					line-height: 1;
					font-size: 30px;
					color: #999;
				}

				.txt {
					display: block;
					width: 100%;
					line-height: 1.14;
					font-size: 8px;
					color: #888;
					letter-spacing: 1px;
					text-align: center;
				}
			}
		}
	}

	.freedial-false {
		font-size: 10px;
	}
}