.salon-info {
  margin-bottom: 62px;

  @media (min-width: $md-min) {
    max-width: 720px;
    margin: 0 auto 62px;
  }
}

.salon-name {
  display: flex;
  align-items: center;
  align-items: baseline;
  margin-bottom: 10px;
  padding-bottom: 10px;
  font-weight: normal;
  border-bottom: 1px solid #ddd;

  .en {
    font-size: 24px;
    color: #454545;
    vertical-align: -2px;

    @media (min-width: $md-min) {
      font-size: 38px;
    }
  }
  .jp {
    font-size: 13px;
    color: #888;
    @media (min-width: $md-min) {
      font-size: 15px;
    }
  }
  .slash {
    margin-left: 12px;
    margin-right: 12px;
    // vertical-align: 1px;
  }
}

.salon-info-table {
  width: 100%;
  th {
    padding-right: 14px;
    font-weight: normal;
    color: #888;
    letter-spacing: 1px;

    @media (max-width: $sm-max) {
      padding-bottom: 8px;
      font-size: 1.5rem;
    }

    @media (min-width: $md-min) {
      width: 100px;
    }
  }
  tr {
    @media (max-width: $sm-max) {
      display: block;
      margin-bottom: 1.4rem;
      padding-bottom: 8px;
      border-bottom: 1px solid #ddd;
    }
  }
  th, td {
    // padding-top: 8px;
    // padding-bottom: 8px;

    @media (max-width: $sm-max) {
      display: block;
      text-align: left;
      border: 0 !important;
    }

    @media (min-width: $md-min) {
      padding-top: 12px;
      padding-bottom: 12px;
      vertical-align: top;
      border-bottom: 1px solid #ddd;
    }
  }
  td {
    font-size: 1.4rem;
    letter-spacing: 1px;
  }
}

.salon-photo-map {
  margin-bottom: 62px;

  @media (min-width: $md-min) {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -15px;
    margin-right: -15px;
  }
  img {
    max-width: 100%;
    height: auto;
    backface-visibility: hidden;
  }
  // img {
  // 	position: absolute;
  // 	top: 0;
  // 	left: 0;
  // 	right: 0;
  // 	bottom: 0;
  // 	width: 100%;
  // 	height: auto;
  // 	margin: auto;
  // }
  .item {
    position: relative;
    max-width: 620px;
    margin: 0 auto;
    padding: 15px;
    // height: 0;
    // padding-bottom: 67.747748%;
    // overflow: hidden;
    background-color: #fafeff;
  }
  .image-map {
    border: 1px solid #ddd;
    @media (max-width: $screen-tab - 1) {
      margin-top: 24px;
    }
  }
}

.salon-access-map {
  margin-bottom: 64px;
  .acf-map {
    width: 100%;
    height: 0;
    padding-bottom: 47.847358%;
    overflow: hidden;
  }
  .gm-style {
    .attachment-logo {
      width: 140px;
      height: auto;
    }
  }
  @media (max-width: $screen-tab - 1) {
    .acf-map {
      padding-bottom: 74%;
    }
  }
}
.salon-access-flex {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin-bottom: 24px;
  padding-bottom: 12px;
  border-bottom: 1px solid #ddd;
  .btn {
    margin-left: auto;
  }
  @media (max-width: $screen-tab - 1) {
    .btn-ls {
      padding: 6px 10px;
      font-size: 13px;
    }
  }
  @media (min-width: $screen-tab) {
    margin-bottom: 32px;
    padding-bottom: 16px;
  }
}
.salon-access-title {
  margin: 0;
  font-weight: normal;
  .en {
    font-size: 28px;
  }
  @media (min-width: $screen-tab) {
    .en {
      font-size: 36px;
    }
  }
}

.salon-access-route {
  margin-top: 20px;
  font-size: 13px;
  color: #454545;
  background-color: #f5f5f5;
  p {
    margin-bottom: 1em;
    &:last-child {
      margin-bottom: 0;
    }
  }
  @media (max-width: $screen-tab - 1) {
    padding: 12px;
  }
  @media (min-width: $screen-tab) {
    margin-top: 32px;
    padding: 28px 20px;
  }
}
